.title{display:flex;flex-direction:column;align-items:flex-start;grid-column:1/-1;gap:.25em;padding:12vh 0}.title div{display:flex;align-items:flex-start;overflow:hidden}.title div span{font-size:var(--display-size);font-weight:var(--display-weight);line-height:var(--display-height);letter-spacing:var(--display-spacing);text-transform:uppercase;opacity:0}.title div span.-loaded{animation:taglineSlideUp 1s var(--ease) forwards}@keyframes taglineSlideUp{from{transform:translateY(100%);opacity:0}to{transform:translateY(0);opacity:1}}.title.-medium{grid-column:auto;padding:0 0 calc(var(--margin-row)*1.5)}.title.-medium div span{font-size:var(--title-size);font-weight:var(--title-weight);line-height:var(--title-height);letter-spacing:var(--title-spacing)}.title.-small{grid-column:auto;padding:0 0 var(--margin-row)}.title.-small div span{font-size:var(--headline-size);font-weight:var(--headline-weight);line-height:var(--headline-height);letter-spacing:var(--headline-spacing)}.title.-fill{grid-column:1/-1}