.numeric{display:flex;flex-direction:column;justify-content:flex-end;gap:1rem}.numeric p{text-wrap:nowrap}.numeric div{display:flex;align-items:flex-end;gap:.125em;padding-bottom:1rem;border-bottom:2px solid var(--foreground-color)}.numeric div span{font-weight:500;line-height:1}.numeric div span:first-child,.numeric div span:last-child{font-size:var(--display-size);font-weight:var(--display-weight);line-height:var(--display-height);letter-spacing:var(--display-spacing)}@media screen and (max-width: 1440px){.numeric div span:first-child,.numeric div span:last-child{font-size:5rem}}.numeric.-medium div span:last-child{margin-bottom:.375em;font-size:var(--headline-size);font-weight:var(--headline-weight);line-height:var(--headline-height);letter-spacing:var(--headline-spacing)}