.language{display:flex;flex-direction:column;gap:0;height:calc(var(--font-sub-size)*var(--font-sub-height));padding-left:1rem;margin-left:1rem;border-left:1px solid var(--foreground-dimmed-color);overflow:hidden}.language button{color:var(--foreground-color);font-size:var(--font-sub-size);font-weight:var(--font-sub-weight);line-height:var(--font-sub-height);letter-spacing:var(--font-sub-spacing);text-transform:uppercase;text-wrap:nowrap;transition:all .6s var(--ease)}.language button.-hidden{opacity:0;pointer-events:none}