.cell{display:flex;flex-direction:column;gap:var(--grid-gap);padding-bottom:var(--margin-row)}.cell:last-child{padding:unset}.cell.-fill{grid-column:1/-1}.cell.-grid{display:grid;grid-template-columns:repeat(2, 1fr)}.cell.-btwn{flex-direction:row;justify-content:space-between;gap:unset}.cell.-list{display:grid;grid-template-columns:repeat(2, 1fr);grid-column:1/-1;-moz-column-gap:var(--grid-gap);column-gap:var(--grid-gap);padding-top:var(--margin-row);border-top:2px solid var(--border-color)}.cell.-list .-btwn{align-items:center}.cell.-center{align-items:center}.cell.-end{align-items:flex-end}@media screen and (max-width: 820px){.cell{grid-column:1/-1}.cell.-grid,.cell.-list{grid-template-columns:100%}}.null{visibility:hidden;pointer-events:none}