section{z-index:1;display:grid;grid-template-columns:repeat(2, 1fr);-moz-column-gap:var(--grid-gap);column-gap:var(--grid-gap);width:100%;padding:calc(var(--margin-row)*2) var(--margin);background:var(--background-color);overflow:hidden}section.-hero{padding-top:calc(var(--margin-row)*4);padding-bottom:unset}section.-hero .title{justify-content:center}section.-intro{padding-top:unset}section.-cover{position:sticky;z-index:-1;display:flex;align-items:center;top:0;right:0;bottom:0;left:0;width:100%;height:100vh}section.-cover::after{background:var(--black-color)}section.-cover video{position:absolute;left:50%;width:100vw;height:100%;background-image:unset;outline:unset;transform:translateX(-50%);-o-object-fit:cover;object-fit:cover;-o-object-position:50% 50%;object-position:50% 50%;pointer-events:none;filter:brightness(80%)}section.-footer{background:var(--background-dimmed-color)}section.-footer .linktext .box *{text-transform:none}@media screen and (max-width: 820px){section{grid-template-columns:100%}}