.text{font-size:var(--font-size);font-weight:var(--font-weight);line-height:var(--font-height);letter-spacing:var(--font-spacing)}.text.-large{font-size:var(--font-label-size);font-weight:var(--font-label-weight);line-height:var(--font-label-height);letter-spacing:var(--font-label-spacing)}.text.-small{font-size:var(--font-sub-size);font-weight:var(--font-sub-weight);line-height:var(--font-sub-height);letter-spacing:var(--font-sub-spacing);text-transform:uppercase;text-wrap:nowrap}.text.-end{text-align:end}.text.-dim{opacity:.5}