.img{padding-top:56.25%;background:rgba(0,0,0,0);background-image:repeating-linear-gradient(135deg, var(--border-color), var(--border-color) 2px, var(--background-color) 2px, var(--background-color) 1rem);border-radius:.5rem;outline:2px solid var(--background-color);outline-offset:-1px;overflow:hidden}.img.-oxo{padding-top:100%}.img.-fxt{padding-top:75%}.img.-lsp{padding-top:28.125%}@media screen and (max-width: 1440px){.img.-lsp{padding-top:56.25%}}.img.-prt{width:calc((100% - var(--grid-gap))*.25);padding:unset}.img.-prt img{position:relative;top:auto;left:auto;transform:unset}.img div{z-index:1;position:absolute;display:flex;align-items:center;justify-content:center;top:50%;left:50%;transform:translate(-50%, -50%);overflow:hidden;pointer-events:none}.img div span{color:var(--white-color);font-size:var(--font-sub-size);font-weight:var(--font-sub-weight);line-height:var(--font-sub-height);letter-spacing:var(--font-sub-spacing);text-transform:uppercase;text-wrap:nowrap;transform:translateY(100%);opacity:0}.img div span,.img img{display:block;transition:all .6s var(--ease)}.img img{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.-hover .img div span{transform:translateY(0);opacity:1}.-hover .img img{transform:translate(-50%, -50%) scale(108%);filter:brightness(64%)}video{background:rgba(0,0,0,0);background-image:repeating-linear-gradient(135deg, var(--border-color), var(--border-color) 2px, var(--background-color) 2px, var(--background-color) 1rem);outline:2px solid var(--background-color);outline-offset:-1px;overflow:hidden}