.fab{position:fixed;z-index:999;display:flex;align-items:center;justify-content:center;bottom:1em;padding:1em;font-size:var(--font-size);font-weight:var(--font-weight);line-height:1;letter-spacing:var(--font-spacing);text-wrap:nowrap;cursor:pointer}.fab.-contact{left:50%;min-width:320px;color:var(--foreground-color);background:rgba(var(--black-rgb), 0.5);border-radius:.5em;-webkit-backdrop-filter:blur(0.5rem);backdrop-filter:blur(0.5rem);transition:all 1s var(--ease)}.fab.-contact::before{content:"";width:.375em;height:.375em;margin-right:.75em;background:var(--green-color);border-radius:50%;animation:statusBlink 2s infinite}@keyframes statusBlink{50%{opacity:.5}}.fab.-contact div{padding:.5rem 0;margin-left:var(--grid-gap);background:var(--foreground-color);border-radius:calc((var(--font-sub-size) + 1rem)*.5);overflow:hidden}.fab.-contact div span{display:block;color:var(--background-color);font-size:var(--font-sub-size);font-weight:var(--font-sub-weight);text-transform:uppercase;animation:textSlideIn 4s linear infinite}.fab.-contact div span::after{content:"Get in touch    ";position:absolute;width:160px;line-height:inherit}@keyframes textSlideIn{0%{transform:translateX(0%)}100%{transform:translateX(-100%)}}.fab.-scroll{right:1em;width:calc(var(--font-size) + 1.5em);height:calc(var(--font-size) + 1.5em);color:var(--background-color);background:var(--foreground-color);border-radius:.25em;font-weight:900;transition:all .4s var(--ease)}